body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
    background: #222;
    font-family: 'Montserrat', sans-serif;
    font-family: 'Oswald', sans-serif;
    background-image: url('https://images.unsplash.com/photo-1477959858617-67f85cf4f1df?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=944&q=80');
    background-position: center;
    background-size: cover;
}
.card {
    background: #0a3641d0;
    color: rgb(255, 255, 255);
    padding: 2em;
    border-radius: 30px;
    width: 100%;
    max-width: 420px;
    margin:  1em;
}

.search{
    display: flex;
    align-items: center;
    justify-content: center;

}

button {
    margin: 0.5em;
    border: none;
    border-radius: 50%;
    height: 44px;
    width: 44px;
    outline: none;
    background: #7c7c7c2b;
    color: white;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

input.search-bar{
    border: none;
    outline: none;
    padding: 0.4em 1em;
    border-radius: 24px;
    background: #7c7c7c2b;
    color: white;
    font-family: inherit;
    font-size: 120%;
    width: calc(100%-100px);
}

button:hover{
    background: #7c7c7c6b;
}

h1.temp {
    margin: 0;
    margin-bottom: 0.5em;
}

.flex {
    display: flex;
    align-items: center;
}

.description {
    text-transform: capitalize;
    margin-left: 8px;
}

.weather.loading {
   visibility: hidden; 
   max-height: 20px;
   position: relative;
}

.weather.loading:after {
    visibility: visible;
    content: "Loading...";
    color: white;
    position: absolute;
    top: 0;
    left: 20px;
}